.features {
  padding-top: 80px;
  padding-bottom: 80px;

  &__list {
    padding: 0 36px;

    @media (min-width: $media-medium) {
      display: flex;
      flex-wrap: wrap;
      padding: 0 4px;
    }
    @media (min-width: $media-large) {
      padding: 0;
      margin: 0 -32px;
    }
  }

  &__item {
    margin-bottom: 64px;

    &:last-child {
      margin-bottom: 0;
    }
    
    @media (min-width: $media-medium) {
      flex-basis: 50%;
      padding: 0 20px;  
    }
    @media (min-width: $media-large) {
      flex-basis: 33.33%;
      padding: 0 32px; 
    }
  }
}


.feature {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__image {
    width: 72px;
    height: 72px;
    margin-bottom: 12px;
  }

  &__text {
    text-align: center;
    font-size: 16px;
    line-height: 24px;
    font-weight: 600;
  }
}