.cta {
  background: linear-gradient(162.95deg, rgba(216, 114, 53, 0.7) 0%, rgba(244, 199, 96, 0.7) 100%), #FCF8E3;

  &__inner {
    padding: 40px 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__image {
    width: 64px;
    height: 64px;
    margin-bottom: 16px;
  }

  &__heading {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    margin-bottom: 8px;
  }

  &__description {
    font-family: "Inter", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    margin-bottom: 32px;
  }

  &__buttons {
    height: 60px;
    width: 100%;
    background: black;
  }
}