.courses {
  padding-top: 80px;
  padding-bottom: 80px;

  &__item {
    margin-bottom: 80px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__description {
    padding: 80px 20px 0;

    &-heading {
      font-weight: 800;
      font-size: 28px;
      line-height: 33px;
      margin-bottom: 48px;
    }
  }

  &__skills-item {
    margin-bottom: 48px;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}


.course {
  border-radius: 16px;
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
  min-height: 448px;
  line-height: 0;

  @media (min-width: $media-medium) {
    box-shadow: none;
    border-radius: 0;
    min-height: auto;
  }

  &__image {
    max-width: 100%;
    border-radius: 16px 16px 0 0;

    @media (min-width: $media-medium) {
      box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
      border-radius: 16px;
    }
  }

  &__content {
    padding: 0 24px 64px;

    @media (min-width: $media-medium) {
      padding: 24px 40px 0;
      display: flex;
    }
  }
  
  &__icon {
    position: relative;
    margin-top: -32px;
    margin-bottom: 32px;
    width: 64px;
    height: 64px;
    filter: drop-shadow(0px 4px 8px rgba(0, 0, 0, 0.2));
    border-radius: 12px;

    @media (min-width: $media-medium) {
      margin-top: 0;
      margin-bottom: 0;
      margin-right: 24px;
    }
  }

  &__heading {
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 8px;
  }

  &__captions {
    font-family: "Inter", sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 0.4);
    margin-bottom: 8px;
  }

  &__price {
    color: #FE5F17;
  }

  &__description {
    font-family: "Inter", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: rgba(0, 0, 0, 0.4)
  }

}