.steps {
  &__inner {
    position: relative;
    padding-top: 42px;
    padding-bottom: 62px;

    @media (min-width: $media-medium) {
      padding-top: 102px;
      padding-bottom: 132px;
    }

    @media (min-width: $media-large) {
      padding-top: 69px;
      padding-bottom: 109px;
    }
  }

  &__content {
    padding: 0 20px;
    color: #fff;
    @media (min-width: $media-medium) {
      padding: 0 24px;
    }
    @media (min-width: $media-medium) {
      padding: 0 80px;
    }
  }

  &__heading {
    font-weight: 800;
    font-size: 28px;
    line-height: 33px;
    margin-bottom: 32px;
  }

  &__item {
    margin-bottom: 32px;

    &:last-child {
      margin-bottom: 0;
    }

    img {
      width: 24px;
      height: 24px;
      margin-bottom: 8px;
    }

    p {
      font-weight: 800;
      font-size: 24px;
      line-height: 28px;
    }
  }
}

.sticky {
  background: linear-gradient(162.95deg, rgba(231, 77, 232, 0.7) 0%, rgba(247, 200, 107, 0.7) 100%), no-repeat center / cover url(../img/steps-bg.jpeg);
  position: sticky;
  top: 0;
  width: 100vw;
  height: 100vh;

  @media (min-width: $media-large) {
    margin: 0 auto;
    max-width: 860px;
    border-radius: 16px;
  }
}